import { Injectable } from '@angular/core';
import { WebApiProvider } from '../providers';
import { Observable } from 'rxjs';

export class TokenResponse {
  accessToken: string;
  code?: any;
  details?: string;
}

@Injectable({ providedIn: 'root' })
export class LoginService {
  constructor(private webApiProvider: WebApiProvider) { }

  public getUserToken(username: string, password: string): Observable<TokenResponse> {
    return this.webApiProvider.postAuth<TokenResponse>('auth/v1/signin', {'username' : username, 'password' : password}, true);
  }
}
