import { Component, OnInit } from '@angular/core';
import { SharedAnimations } from 'src/app/shared/animations/shared-animations';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AuthService } from '../../../shared/services/auth.service';
import { Router, RouteConfigLoadStart, ResolveStart, RouteConfigLoadEnd, ResolveEnd } from '@angular/router';
import { LoginService } from 'src/app/shared/services/login.service';
import { HttpErrorResponse } from '@angular/common/http';
import  *  as jwt_decode from 'jwt-decode';
import { LocalStoreService } from 'src/app/shared/services/local-store.service';
import { decode } from 'punycode';


@Component({
    selector: 'app-signin',
    templateUrl: './signin.component.html',
    styleUrls: ['./signin.component.scss'],
    animations: [SharedAnimations]
})
export class SigninComponent implements OnInit {
    loading: boolean;
    loadingText: string;
    signinForm: FormGroup;
    token: string;

    error: HttpErrorResponse = null;
    errorMessage: string = null;

    constructor(
        private fb: FormBuilder,
        private auth: AuthService,
        private loginService: LoginService,
        private router: Router,
        private localStorage: LocalStoreService
    ) { }

    ngOnInit() {

        this.signinForm = this.fb.group({
            userName: '',
            password: ''
        });

        this.router.events.subscribe(event => {
            if (event instanceof RouteConfigLoadStart || event instanceof ResolveStart) {
                this.loadingText = 'Loading Dashboard Module...';

                this.loading = true;
            }
            if (event instanceof RouteConfigLoadEnd || event instanceof ResolveEnd) {
                this.loading = false;
            }
        });

    }

    signin() {
        this.loading = true;
        this.loadingText = 'Sigining in...';
        this.getUserToken();
    }


    getUserToken() {
        const username = this.signinForm.controls.userName.value;
        const password = this.signinForm.controls.password.value;
        this.loginService.getUserToken(username, password).subscribe(response => {
            if (response) {
                this.loading = false;
                this.token = response.accessToken;
                const decodedToken = jwt_decode(this.token);
                console.log(decodedToken);
                if (decodedToken) {
                    const userName = decodedToken.sub;
                    this.localStorage.setItem('userName', userName);
                    this.localStorage.setItem('isAuthenticated', true);
                }
                this.localStorage.setItem('token', this.token);
                console.log(this.token);
                this.router.navigate(['/dashboard']);
            } else {
                this.router.navigate(['/login']);
                this.errorMessage = response.details;
            }
        },
            error => {
                this.loading = false;
                if (error.status > 400 && error.status < 500) {
                    this.errorMessage = 'Invalid username/password. Please try again.';
                } else {
                    error.status = '500';
                    this.errorMessage = 'Something went wrong ! Please try again.';
                }
                this.error = error;
                this.router.navigate(['/login', { error: this.error, errorMessage: this.errorMessage }]);
            }
        );
    }

}
